round: #EEDC94;\r\n}\r\n\r\n.fallback-message p {\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.impress-supported .fallback-message {\r\n    display: none;\r\n}\r\n\r\n\r\n/*\r\n  The body background is the bacgkround of \"everything\". Many\r\n  impress.js tools call it the \"surface\". It could also be a\r\n  picture or pattern, but we leave it as light gray.\r\n*/\r\n\r\nbody {\r\n    font-family: 'PT Sans', sans-serif;\r\n    min-height: 740px;\r\n\r\n    background: #aaccbb;\r\n    color: #ff4466;\r\n}\r\n\r\n/*\r\n    Now let's style the presentation steps.\r\n*/\r\n\r\n.step {\r\n    position: relative;\r\n    display: block;\r\n\r\n    width: 900px;\r\n    height: 700px;\r\n    margin: 20px auto;\r\n    padding: 40px 60px;\r\n\r\n    text-shadow: 0 2px 2px rgba(0, 10, 0, .5);\r\n\r\n    font-family: 'Open Sans', Arial, sans-serif;\r\n    font-size: 30px;\r\n    letter-spacing: -1px;\r\n\r\n}\r\n/*\r\n    Make inactive steps a little bit transparent.\r\n*/\r\n.impress-enabled .step {\r\n    margin: 0;\r\n    opacity: 0.3;\r\n    transition:         opacity 1s;\r\n}\r\n\r\n.impress-enabled .step.active { opacity: 1 }\r\n\r\n/*\r\n    Speaker notes allow you to write comments within the steps, that will not\r\n    be displayed as part of the presentation. However, they will be picked up\r\n    and displayed by impressConsole.js when integrated.\r\n*/\r\n\r\n.notes {\r\n    display: none;\r\n}\r\n\r\nh1,\r\nh2,\r\nh3 {\r\n    margin-bottom: 0.5em;\r\n    margin-top: 0.5em;\r\n    text-align: center;\r\n}\r\n\r\np {\r\n    text-align: center;\r\n    margin: 0.7em;\r\n}\r\n\r\nli {\r\n    margin: 0.2em;\r\n}\r\n\r\n/* Highlight.js used for coloring pre > code blocks. */\r\npre > code {\r\n    font-size: 14px;\r\n    text-shadow: 0 0 0 rgba(0, 0, 0, 0);\r\n}\r\n\r\n/* Inline code, no Highlight.js */\r\ncode {\r\n    font-family: \"Cutive mono\",\"Courier New\", monospace;\r\n}\r\n\r\n\r\na {\r\n    color: inherit;\r\n    text-decoration: none;\r\n    padding: 0 0.1em;\r